import React, { useState } from "react";
import styles from "./CountryDropdown.module.scss";
import { appConfig } from "../../config";

const CountryDropdown = () => {
  const [open, setOpen] = useState(false);
  const toggleDropdown = () => setOpen(!open);

  return (
    <>
      <div className={styles.navDropdownBtn} onClick={toggleDropdown}>
        <div className={styles.selectContainer}>
          <p>Country</p> <i className="fas fa-caret-down"></i>
        </div>
        {open && (
          <div className={styles.countryDropdown} role="menu">
            {appConfig.navLinks.map((route, index) => (
              <a href={route.route} className={styles.dropdownItem} onClick={toggleDropdown} key={index} role="menuitem">
                {route.country}
              </a>
            ))}
          </div>
        )}
      </div>
    </>
  );
};

export { CountryDropdown };
